import {
    MAP_INIT,
} from '../constants/Map';

export function initMap(source,width,height) {

    const mapProps = {
        source,
        width,
        height,
    };
    console.log(mapProps);
    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: mapProps
        });

    }
}

export function changedMapSource(source) {

    return (dispatch) => {
        dispatch({
            type: MAP_INIT,
            payload: source
        });

    }
}
